main {
  width: 944px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 15px 14px;
  margin: 0 48px 50px 0;
  width: 200px;
  &:nth-child(4n) {
    margin-right: 0;
  }
  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
  }
}

.card__foto {
  width: 160px;
  height: 134px;
  margin: 15px 0 41px 5px;
}

.card__name {
  margin: 0 0 5px;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
}

.card__description {
  margin-bottom: 15px;
}

.small--text {
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
}

.rate {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.rate__star--4 {
  display: flex;
}

.star {
  width: 15px;
  height: 15px;
  background: url(../images/star.svg) center no-repeat;
  margin-right: 4px;
}

.rate__star--1 > :nth-child(-n+1),
.rate__star--2 > :nth-child(-n+2),
.rate__star--3 > :nth-child(-n+3),
.rate__star--4 > :nth-child(-n+4),
.rate__star--5 > :nth-child(-n+5) {
  background-image: url(../images/star-active.svg);
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 20px;
}

.card__price--value {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
}

.card__button {
  width: 170px;
  height: 40px;
  background: #00acdc;
  border-radius: 5px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  &:hover {
    background: #fff;
    border: 1px solid #00acdc;
    box-sizing: border-box;
    border-radius: 5px;
    color: #00acdc;
  }
}
